.footer {
  background: #081630;
  margin-top: 100px;
}

.footer-main {
  display: flex;
  justify-content: space-evenly;
  align-items: baseline;
  padding-top: 100px;
}

.footer-links {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.footer-link-title {
  color: #fff;
  font-family: DM Serif Display;
  font-size: 28px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: capitalize;
}

.footer-link {
  color: #fff;
  font-family: Roboto;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: capitalize;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0;
}

.footer-link li {
  list-style: none;
  margin-top: 10px;
  color: #fff;
  font-family: Roboto;
  font-size: 25px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  text-transform: capitalize;
}

.footer-line {
  margin: 0 auto;
  border-top: 1px solid #fff;
  width: 70%;
}

.footer-logo {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 30px;
}

.footer-logo h1 {
  color: #fff;
  font-family: Roboto;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.footer-social-networks {
  display: flex;
}
.footer-social-networks svg {
  margin-left: 10px;
}
